/**
 *  , 
 *
 * @format
 */

export const Timer = class {
  /**/
  #subscribeList = [];
  /**/
  #currentTime = 0;
  /**/
  #interval;
  /*settimeout clearID */
  #currentTimeout;
  /**/
  #status = false;
  /**
   * @param remaining {number}
   */
  constructor() {
    const interval = () => {
      this.#status = true;
      if (this.#currentTime >= 1000) {
        this.#currentTimeout = setTimeout(() => {
          this.#currentTime -= 1000;
          interval();
          this.#subscribeList.forEach(fn => {
            try {
              fn(this.#currentTime);
            } catch (e) {
              console.error(e, `Timer : >${fn.name}`);
            }
          });
        }, 1000);
      } else {
        /*1000 */
        this.#currentTimeout = setTimeout(() => {
          this.#currentTime = 0;
          this.#subscribeList.forEach(fn => {
            try {
              fn(this.#currentTime);
            } catch (e) {
              console.error(e, `Timer : >${fn.name}`);
            }
          });
        }, this.#currentTime);
        this.#status = false;
      }
      /**/
      this.#subscribeList.forEach(fn => {
        try {
          fn(this.#currentTime);
        } catch (e) {
          console.error(e, `Timer : >${fn.name}`);
        }
      });
    };
    this.#interval = interval;
  }
  start = time => {
    if (time <= 0) {
      return console.error('timer.start! :>', time);
    }
    /*,start*/
    if (this.#status === true) {
      return;
    }
    this.#currentTime = time;
    this.#interval();
    return this;
  };

  /**
   * 1s[]
   * @param callback {function(number):*}
   */
  subscribe(callback) {
    !this.#subscribeList.includes(callback) && this.#subscribeList.push(callback);
    return this;
  }
  unsubscribe(callback) {
    this.#subscribeList.splice(this.#subscribeList.indexOf(callback), 1);
    return this;
  }
  destroy() {
    //todo
  }
  stop() {
    clearTimeout(this.#currentTimeout);
    this.#currentTime = 0;
    this.#status = false;
    return this;
  }
};

