/**
 * @description qs()||:key-valuedecodeURIComponent!!!
 * @param str ,href query
 */

export const decodeQueryString = <result extends string[] = []>( str: string = location.href ): Batch<result , string> => {
	type ret = Batch<result , string>;
	const empty = {} as ret;
	const thr = () : never => {
		throw new Error( `queryString ,  : ${ str }` );
	};
	try {
		/**
		 * case1 : ?queryString 
		 *  : 
		  `csd=123`
		  `vasd=`
		  `=`
		 */
		{
			if(str.includes('?')){
				[,str] = str.split('?');
			}
			if(str.includes('/')){
				[str] = str.split('/');
			}
			
			
			if(!str.includes('=')){
				return empty;
			}
			
			if(str.includes('&')){
				return str.split('&').reduce((accumulator,current) => {
					const [key,value] = current.split('=');
					accumulator[key] = value;
					return accumulator;
				},{} as ret);
			}else {
				const temp = {};
				const [ key , value ] = str.split( '=' );
				temp[ key ] = value;
				return temp as ret;
			}
		}
	} catch ( e ) {
		console.error( `decodeQueryString ->  ,  : ${ str }` );
		console.error( e );
		throw '';
	}
};



/**
 * queryString()
 * @return {string} a=1&b=2
 */
export const encodeQueryString = ( source: object ):string => {
	let result = ``;
	for ( const key in source ) {
		const value = source[ key ];
		if ( typeof value === 'object' && value !== null ) {
			throw '';
		}
		if ( typeof key === 'symbol' ) {
			throw 'keySymbol';
		}
		result = `${ result }${ result && '&' }${ key }=${ value }`;
	}
	return result;
};





