/** @format */

import {useEffect, useRef} from 'react';
/**
 * @description falsetrue
 * @example
 *      const mountedRef = useMountedRef();
 */
export const useMountedRef = () => {
  const mountedRef = useRef(false);

  useEffect(() => {
    mountedRef.current = true;
    return () => {
      mountedRef.current = false;
    };
  });

  return mountedRef;
};
