/**
 * Cookie
 *
 * @format
 */

const Cookie = {
  /**
   * get
   *
   * @description cookie
   *
   * @param {string} key - cookiekey
   * @param {Function} [decode] - value  decodeURIComponent 
   * @returns {string}
   *
   * @example
   *
   *    Cookie.get('test')
   */
  get(key, decode = decodeURIComponent) {
    let value;
    if (key && document.cookie && document.cookie !== '') {
      let cookies = document.cookie.split('; ');
      for (let i = 0; i < cookies.length; i++) {
        let parts = cookies[i].split('=');
        let cookie = parts.slice(1).join('=');
        let name = decode(parts[0]);

        // "
        if (cookie.indexOf('"') === 0) {
          cookie = cookie.slice(1, -1);
        }

        if (key === name) {
          value = decode(cookie);
          break;
        }
      }
    }
    return value;
  },

  /**
   * set
   *
   * @description cookie
   *
   * @param {string} key - cookie  key
   * @param {string|number|object} value - cookie  value
   * @param {object} options - { expires: 0, path: null, secure: true, domain: 'd.2dfire.com' }<br>
   * expires: Date;<br>
   * path:  '/', '/mydir'; ;<br>
   * secure: cookiehttps (boolean | null);<br>
   * domain: 'd.2dfire.com' (string | null)
   *
   * @returns {string}
   *
   * @example
   *
   *    Cookie.set('test', 'xxx', { expires: 0, path: null, secure: true, domain: 'd.2dfire.com' })
   *
   */
  set(key, value, options) {
    options = Object.assign(
      {},
      {
        encode: encodeURIComponent,
        expires: 365,
        path: '/',
        secure: window.location.protocol === 'https:',
      },
      options,
    );

    let expires = options.expires;
    //Date
    if (typeof expires === 'number' && expires !== 0 && isFinite(expires)) {
      expires = new Date(new Date().getTime() + expires * 864e5);
    }
    //value
    if (typeof value === 'object') {
      try {
        value = JSON.stringify(value);
      } catch (e) {}
    }
    //keyvalueencodeURIComponentencode
    key = options.encode(key);
    value = options.encode(value);

    return (document.cookie = [
      key + '=' + value,
      expires ? '; expires=' + expires.toUTCString() : '',
      options.path ? '; path=' + options.path : '',
      options.domain ? '; domain=' + options.domain : '',
      options.secure ? '; secure' : '',
    ].join(''));
  },

  /**
   * remove
   *
   * @description cookie
   *
   * @param {string} key - cookie  key
   * @returns {boolean}
   *
   * @example
   *
   *  Cookie.remove('test')
   */
  remove(key) {
    this.set(key, '', {
      expires: -1,
    });
    return !this.get(key);
  },

  hasItem: function (sKey) {
    return new RegExp('(?:|;s*)' + encodeURIComponent(sKey).replace(/[-.+]/g, '$&') + 's=').test(document.cookie);
  },
};

export default Cookie;
