/**
 *
 * Random
 *
 */

const Random = {
  /**
   * color
   *
   * @description 
   * @function
   * @returns {string} HEX
   * @example
   *
   *    Random.color() // => '#000000'
   */
  color() {
    return '#' + Math.floor(Math.random() * 0xffffff).toString(16);
  },

  /**
   * number
   *
   * @description [min, max]
   * @param {number} min - 
   * @param {number} max - 
   * @param {boolean|number} float - false
   * @function
   * @returns {number} 
   * @example
   *
   *    Random.number(1, 100) // => 50
   *    Random.number(1, 100, true) // => 66.357922312356983
   *    Random.number(1, 100, 1) // => 29.1
   *    Random.number(1, 100, 3) // => 61.232
   */
  number(min: number, max: number, float?: number | boolean) {
    if (min === undefined) {
      return Math.random();
    }

    if (max === undefined) {
      max = min;
      min = 0;
    }

    if (max < min) {
      let tmp = max;
      max = min;
      min = tmp;
    }

    if (float) {
      let result = Math.random() * (max - min) + min;
      if (float === true) {
        return result;
      } else if (typeof float === 'number') {
        let str = result.toString();
        let index = str.indexOf('.');
        str = str.substr(0, index + 1 + float);
        if (str[str.length - 1] === '0') {
          str = str.substr(0, str.length - 1) + _.random(1, 9);
        }
        return parseFloat(str);
      }
    }

    return Math.floor(Math.random() * (max - min + 1) + min);
  },
};

export default Random;
